import { Input, Form, Row, Col } from "antd";
import "./container.css";
import { SendRequestWebService } from "../../controlers/services/userRequest";
import { useState, useEffect } from "react";
import { FieldData } from "./containerInterface";

export let userAprover: any = {
  nomfun: "",
  numcad: "",
  numemp: "",
  tipcol: "",
  titred: "",
  codcar: "",
};

export default function ContainerIdentify() {
  const [fields, setFields] = useState<FieldData[]>([
    { name: "fullname", value: "" },
    { name: "registry", value: "" },
    { name: "subsidiary", value: "" },
    { name: "office", value: "" },
    { name: "boss", value: "" },
  ]);

  const [dados, setDados] = useState<any>([
    {
      nomfun: "",
      numcad: "",
      numemp: "",
      tipcol: "",
      titred: "",
      codcar: "",
    },
  ]);

  const paramName = {
    nomfun: "",
  };

  const dataUserRequest = SendRequestWebService(
    "POST",
    "br.com.cvc.wf.rescisao",
    "GetDadosColaborador",
    paramName
  );

  useEffect(() => {
    dataUserRequest.then((resp: any) => {
      setDados({
        ...dados,
        nomfun: resp.nomfun,
        numcad: resp.numcad,
        numemp: resp.numemp,
        tipcol: resp.tipcol,
        titred: resp.titred,
        codcar: resp.codcar,
      });
      setFields([
        ...fields,
        { name: ["fullname"], value: resp.nomfun },
        { name: ["registry"], value: resp.numcad },
        { name: ["subsidiary"], value: resp.nomccu },
        { name: ["office"], value: resp.nomfil },
        { name: ["boss"], value: resp.supime },
      ]);
    });
  }, []);

  userAprover = {
    nomfun: dados.nomfun,
    numcad: dados.numcad,
    numemp: dados.numemp,
    tipcol: dados.tipcol,
    titred: dados.titred,
    codcar: dados.codcar,
  };

  //console.log(userAprover);

  return (
    <>
      <fieldset>
        <legend>Identificação do Solicitante</legend>
        <Form
          labelCol={{ span: 10 }}
          wrapperCol={{ span: 12 }}
          labelAlign="left"
          fields={fields}
          onFieldsChange={(_, allFields) => {}}
        >
          <Row gutter={50}>
            <Col span={12}>
              <Form.Item name={"fullname"} label={"Nome Solicitante"}>
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={"registry"} label={"Matrícula"}>
                <Input readOnly />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={50}>
            <Col span={12}>
              <Form.Item name={"office"} label={"Cargo Solicitante"}>
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={"subsidiary"} label={"Filial"}>
                <Input readOnly />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={50}>
            <Col span={12}>
              <Form.Item name={"boss"} label={"Superior Imediato"}>
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item></Form.Item>
            </Col>
          </Row>
        </Form>
      </fieldset>
    </>
  );
}
